import { FunctionalComponent, h } from 'preact'
import { useEffect } from 'preact/hooks'
import style from './style.css'

const Bylaws: FunctionalComponent = () => {
    useEffect(() => {
      document.title = 'Psilly By-laws'
    }, [])
    return (
        <div class={style.bylaws}>
            <h1>Psilly By-laws</h1>
<div class="panel panel-default">
  <div class="panel-body">
<p>A <span class="nowrap">by-law</span> relating generally to the conduct of the affairs of</p>


<p><b>Psilly Corporation</b></p>


<p>(the &quot;Corporation&quot;)</p>

<p><b>BE IT ENACTED</b> as a <span class="nowrap">by-law</span> of the Corporation as follows:</p>

<ol class="lst-spcd">

 <li><b>Definitions</b> <p>In this <span class="nowrap">by-law</span> and all other <span class="nowrap">by-laws</span> of the Corporation, unless the context otherwise requires:</p>

<p>&quot;<b>Act</b>&quot; means the <cite><i>Canada <span class="nowrap">Not-For-Profit</span> Corporations Act</i></cite> <abbr title="Statutes of Canada">S.C.</abbr> 2009, <abbr title="chapter">c.</abbr> 23 including the Regulations made pursuant to the Act, and any statute or regulations that may be substituted, as amended from time to time;</p>

<p>&quot;<b>articles</b>&quot; means the original or restated articles of incorporation or articles of amendment, amalgamation, continuance, reorganization, arrangement or revival of the Corporation;</p>

<p>&quot;<b>board</b>&quot; means the board of directors of the Corporation and &quot;director&quot; means a member of the board;</p>

<p><span class="nowrap">&quot;<b>by-law</b>&quot;</span> means this <span class="nowrap">by-law</span> and any other <span class="nowrap">by-law</span> of the Corporation as amended and which are, from time to time, in force and effect;</p>

<p>&quot;<b>meeting of members</b>&quot; includes an annual meeting of members or a special meeting of members; &quot;special meeting of members&quot; includes a meeting of any class or classes of members and a special meeting of all members entitled to vote at an annual meeting of members;</p>

<p>&quot;<b>ordinary resolution</b>&quot; means a resolution passed by a majority of not less than&nbsp;50% plus&nbsp;1 of the votes cast on that resolution;</p>

<p>&quot;<b>proposal</b>&quot; means a proposal submitted by a member of the Corporation that meets the requirements of section 163 (Member Proposals) of the Act;</p>

<p>&quot;<b>Regulations</b>&quot; means the regulations made under the Act, as amended, restated or in effect from time to time; and</p>

<p>&quot;<b>special resolution</b>&quot; means a resolution passed by a majority of not less than <span class="nowrap">two-thirds</span> (2/3)&nbsp;of the votes cast on that resolution.</p>
</li>



 <li><b>Interpretation</b> <p>In the interpretation of this <span class="nowrap">by-law,</span> words in the singular include the plural and <span class="nowrap">vice-versa,</span> words in one gender include all genders, and &quot;person&quot; includes an individual, body corporate, partnership, trust and unincorporated organization.</p>

<p>Other than as specified above, words and expressions defined in the Act have the same meanings when used in these <span class="nowrap">by-laws.</span></p></li>



 <li><b>Corporate Seal</b> <p>The Corporation may have a corporate seal in the form approved from time to time by the board. If a corporate seal is approved by the board, the secretary of the Corporation shall be the custodian of the corporate seal.</p></li>



 <li><b>Execution of Documents</b> <p>Deeds, transfers, assignments, contracts, obligations and other instruments in writing requiring execution by the Corporation may be signed by any two (2) of its officers or directors. In addition, the board may from time to time direct the manner in which and the person or persons by whom a particular document or type of document shall be executed. Any person authorized to sign any document may affix the corporate seal (if any) to the document. Any signing officer may certify a copy of any instrument, resolution, <span class="nowrap">by-law</span> or other document of the Corporation to be a true copy thereof.</p></li>




 <li><b>Financial Year End</b>
 <p>The financial year end of the Corporation shall be December 31 in each year.</p>
 </li>



 <li><b>Banking Arrangements</b> <p>The banking business of the Corporation shall be transacted at such bank, trust company or other firm or corporation carrying on a banking business in Canada or elsewhere as the board of directors may designate, appoint or authorize from time to time by resolution. The banking business or any part of it shall be transacted by an officer or officers of the Corporation and/or other persons as the board of directors may by resolution from time to time designate, direct or authorize.</p></li>



 <li><b>Borrowing Powers</b> <p>The directors of the Corporation may, without authorization of the members,</p>

<ol type="a" class="lst-lwr-alph mrgn-lft-lg">
   <li>borrow money on the credit of the corporation;</li>
   <li>issue, reissue, sell, pledge or hypothecate debt obligations of the corporation;</li>
   <li>give a guarantee on behalf and</li>
   <li>mortgage, hypothecate, pledge or otherwise create a security interest in all or any property of the corporation, owned or subsequently acquired, to secure any debt obligation of the corporation.</li>
</ol></li>



 <li><b>Annual Financial Statements</b> <p>The Corporation shall send to the members a copy of the annual financial statements and other documents referred to in subsection&nbsp;172(1) (Annual Financial Statements) of the Act or a copy of a publication of the Corporation reproducing the information contained in the documents. Instead of sending the documents, the Corporation may send a summary to each member along with a notice informing the member of the procedure for obtaining a copy of the documents themselves free of charge. The Corporation is not required to send the documents or a summary to a member who, in writing, declines to receive such documents.</p></li>



 <li><b>Membership Conditions</b> <p>Subject to the articles, there shall be one class of members in the Corporation. Membership in the Corporation shall be available only to individuals interested in furthering the Corporation's purposes and who have applied for and been accepted into membership in the Corporation by resolution of the board or in such other manner as may be determined by the board. Each member shall be entitled to receive notice of, attend and vote at all meetings of the members of the Corporation.</p>

<p>Pursuant to subsection&nbsp;197(1) (Fundamental Change) of the Act, a special resolution of the members is required to make any amendments to this section of the <span class="noWrap">by-laws</span> if those amendments affect membership rights and/or conditions described in paragraphs 197(1)(e), (h), (l) or (m).</p>
</li>



 <li><b>Transferring Membership</b> <p>A membership may only be transferred to the  Corporation. Pursuant to Section&nbsp;197(1) (Fundamental  Change) of the Act, a special resolution of the members is required to make any amendment to add, change or delete this section of the <span class="nowrap">by-laws</span>.</p></li>



 <li><b>Notice of Members Meeting</b> <p>Notice of the time and place of a meeting of members shall be given to each member entitled to vote at the meeting by telephonic, electronic or other communication facility to each member entitled to vote at the meeting, during a period of 21 to 35 days before the day on which the meeting is to be held. If a member requests that the notice be given by <span class="noWrap">non-electronic</span> means, the notice will be sent by mail, courier or personal delivery.</p>

<p>Pursuant to subsection&nbsp;197(1) (Fundamental Change) of the Act, a special resolution of the members is required to make any amendment to the <span class="noWrap">by-laws</span> of the Corporation to change the manner of giving notice to members entitled to vote at a meeting of members.</p></li>



 <li><b>Members Calling a Members' Meeting</b> <p>The board of directors shall call a special meeting of members in accordance with Section&nbsp;167 of the Act, on written requisition of members carrying not less than 5% of the voting rights. If the directors do not call a meeting within <span class="noWrap">twenty-one</span> (21) days of receiving the requisition, any member who signed the requisition may call the meeting.</p></li>



 <li><b>Absentee Voting at Members' Meetings</b> <p>Pursuant to section&nbsp;171(1) (Absentee Voting) of the Act, a member entitled to vote at a meeting of members may vote by means of a telephonic, electronic or other communication facility if the Corporation has a system that:</p>

<ol type="a" class="lst-lwr-alph  mrgn-lft-lg">
   <li>enables the votes to be gathered in a manner that permits their subsequent verification, and</li>
   <li>permits the tallied votes to be presented to the Corporation without it being possible for the Corporation to identify how each member voted.</li>
</ol>

<p>Pursuant to subsection&nbsp;197(1) (Fundamental Change) of the Act, a special resolution of the members is required to make any amendment to the <span class="nowrap">by-laws</span> of the Corporation to change this method of voting by members not in attendance at a meeting of members.</p></li>



 <li><b>Membership Dues</b> <p>Members shall be notified in writing of the membership dues at any time payable by them and, if any are not paid within one (1) calendar month of the membership renewal date the members in default shall automatically cease to be members of the Corporation.</p></li>



 <li><b>Termination of Membership</b> <p>A membership in the Corporation is terminated when:</p>

<ol type="a" class="lst-lwr-alph mrgn-lft-lg">
   <li>the member dies or resigns;</li>
   <li>the member is expelled or their membership is otherwise terminated in accordance with the articles or <span class="nowrap">by-laws</span>;</li>
   <li>the member's term of membership expires; or</li>
   <li>the Corporation is liquidated and dissolved under the Act.</li>
</ol></li>



 <li><b>Effect of Termination of Membership</b> <p>Subject to the articles, upon any termination of membership, the rights of the member, including any rights in the property of the Corporation, automatically cease to exist.</p></li>



 <li><b>Discipline of Members</b> <p>The board shall have authority to suspend or expel any member from the Corporation for any one or more of the following grounds:</p>

<ol type="a" class="lst-lwr-alph mrgn-lft-lg">
   <li>violating any provision of the articles, <span class="nowrap">by-laws,</span> or written policies of the Corporation;</li>
   <li>carrying out any conduct which may be detrimental to the Corporation as determined by the board in its sole discretion;</li>
   <li>for any other reason that the board in its sole and absolute discretion considers to be reasonable, having regard to the purpose of the Corporation.</li>
</ol>

<p>In the event that the board determines that a member should be expelled or suspended from membership in the Corporation, the president, or such other officer as may be designated by the board, shall provide twenty (20) days notice of suspension or expulsion to the member and shall provide reasons for the proposed suspension or expulsion. The member may make written submissions to the president, or such other officer as may be designated by the board, in response to the notice received within such twenty (20) day period. In the event that no written submissions are received by the president, the president, or such other officer as may be designated by the board, may proceed to notify the member that the member is suspended or expelled from membership in the Corporation. If written submissions are received in accordance with this section, the board will consider such submissions in arriving at a final decision and shall notify the member concerning such final decision within a further twenty (20) days from the date of receipt of the submissions. The board's decision shall be final and binding on the member, without any further right of appeal.</p></li>



 <li><b>Proposals Nominating Directors at Annual Members' Meetings</b> <p>Subject to the Regulations under the Act, any proposal may include nominations for the election of directors if the proposal is signed by not less than 5% of members entitled to vote at the meeting at which the proposal is to be presented.</p></li>



 <li><b>Cost of Publishing Proposals for Annual Members' Meetings</b> <p>The member who submitted the proposal shall pay the cost of including the proposal and any statement in the notice of meeting at which the proposal is to be presented unless otherwise provided by ordinary resolution of the members present at the meeting.</p></li>



 <li><b>Place of Members' Meeting</b> <p>Subject to compliance with section&nbsp;159 (Place of Members' Meetings) of the Act, meetings of the members may be held at any place within Canada determined by the board or, if all of the members entitled to vote at such meeting so agree, outside Canada.</p></li>



 <li><b>Persons Entitled to be Present at Members' Meetings</b> <p>Members, <span class="nowrap">non-members,</span> directors and the public accountant of the Corporation are entitled to be present at a meeting of members. However, only those members entitled to vote at the members' meeting according to the provisions of the Act, articles and <span class="nowrap">by-laws</span> are entitled to cast a vote at the meeting.</p>
</li>



 <li><b>Chair of Members' Meetings</b> <p>In the event that the chair of the board and the <span class="nowrap">vice-chair</span> of the board are absent, the members who are present and entitled to vote at the meeting shall choose one of their number to chair the meeting.</p></li>



 <li><b>Quorum at Members' Meetings</b> <p>A quorum at any meeting of the members (unless a greater number of members are required to be present by the Act) shall be a majority of the members entitled to vote at the meeting. If a quorum is present at the opening of a meeting of members, the members present may proceed with the business of the meeting even if a quorum is not present throughout the meeting.</p></li>



 <li><b>Voting at Members' Meetings</b> <p>At any meeting of members every question shall, unless otherwise provided by the articles or <span class="noWrap">by-laws</span> or by the Act, be determined by a majority of the votes cast on the questions. In case of an equality of votes either on a show of hands or on a ballot or on the results of electronic voting, the chair of the meeting in addition to an original vote shall have a second or casting vote.</p></li>



 <li><b>Participation by Electronic Means at Members' Meetings</b> <p>If the Corporation chooses to make available a telephonic, electronic or other communication facility that permits all participants to communicate adequately with each other during a meeting of members, any person entitled to attend such meeting may participate in the meeting by means of such telephonic, electronic or other communication facility in the manner provided by the Act. A person participating in a meeting by such means is deemed to be present at the meeting. Notwithstanding any other provision of this <span class="noWrap">by-law</span>, any person participating in a meeting of members pursuant to this section who is entitled to vote at that meeting may vote, in accordance with the Act, by means of any telephonic, electronic or other communication facility that the Corporation has made available for that purpose.</p></li>



 <li><b>Members' Meeting Held Entirely by Electronic Means</b> <p>Meetings of members may not be held entirely by telephonic, an electronic or other communication facility.</p></li>



 <li><b>Number of Directors</b> <p>The board shall consist of the number of directors specified in the articles. If the articles provide for a minimum and maximum number of directors, the board shall be comprised of the fixed number of directors as determined from time to time by the members by ordinary resolution or, if the ordinary resolution empowers the directors to determine the number, by resolution of the board. In the case of a soliciting corporation the minimum number of directors may not be fewer than three (3), at least two of whom are not officers or employees of the Corporation or its affiliates.</p></li>



 <li><b>Term of Office of Directors</b>
 <p>The directors shall be elected to hold office for a term expiring not later than the close of the second annual meeting of members following the election.</p>
 </li>



 <li><b>Calling of Meetings of Board of Directors</b> <p>Meetings of the board may be called by the chair of the board, the <span class="nowrap">vice-chair</span> of the board or any two (2) directors at any time; provided that for the first organization meeting following incorporation, such meeting may be called by any director or incorporator. If the Corporation has only one director, that director may call and constitute a meeting.</p></li>



 <li><b>Notice of Meeting of Board of Directors</b>
 <p>Notice of the time and place for the holding of a meeting of the board shall be given in the manner provided in the section on giving notice of meeting of directors of this <span class="nowrap">by-law</span> to every director of the Corporation not less than 42 days before the time when the meeting is to be held. Notice of a meeting shall not be necessary if all of the directors are present, and none objects to the holding of the meeting, or if those absent have waived notice of or have otherwise signified their consent to the holding of such meeting. Notice of an adjourned meeting is not required if the time and place of the adjourned meeting is announced at the original meeting. Unless the <span class="nowrap">by-law</span> otherwise provides, no notice of meeting need specify the purpose or the business to be transacted at the meeting except that a notice of meeting of directors shall specify any matter referred to in subsection 138(2) (Limits on Authority) of the Act that is to be dealt with at the meeting.</p>
 </li>



 <li><b>Regular Meetings of the Board of Directors</b> <p>The board may appoint a day or days in any month or months for regular meetings of the board at a place and hour to be named. A copy of any resolution of the board fixing the place and time of such regular meetings of the board shall be sent to each director forthwith after being passed, but no other notice shall be required for any such regular meeting except if subsection&nbsp;136(3) (Notice of Meeting) of the Act requires the purpose thereof or the business to be transacted to be specified in the notice.</p></li>



 <li><b>Voting at Meetings of the Board of Directors</b> <p>At all meetings of the board, every question shall be decided by a majority of the votes cast on the question. In case of an equality of votes, the chair of the meeting in addition to an original vote shall have a second or casting vote.</p></li>



 <li><b>Committees of the Board of Directors</b> <p>The board may from time to time appoint any committee or other advisory body, as it deems necessary or appropriate for such purposes and, subject to the Act, with such powers as the board shall see fit. Any such committee may formulate its own rules of procedure, subject to such regulations or directions as the board may from time to time make. Any committee member may be removed by resolution of the board of directors.</p></li>



 <li><b>Appointment of Officers</b> <p>The board may designate the offices of the Corporation, appoint officers on an annual or more frequent basis, specify their duties and, subject to the Act, delegate to such officers the power to manage the affairs of the Corporation. A director may be appointed to any office of the Corporation. An officer may, but need not be, a director unless these <span class="nowrap">by-laws</span> otherwise provide. Two or more offices may be held by the same person.</p></li>



 <li><b>Officers of the Corporation</b> <p>Unless otherwise specified by the board (which may, subject to the Act modify, restrict or supplement such duties and powers), the offices of the Corporation, if designated and if officers are appointed, shall have the following duties and powers associated with their positions:</p>

<ol type="a" class="lst-lwr-alph mrgn-lft-lg ">
   <li>Chair of the Board &#8211; The chair of the board, if one is to be appointed, shall be a director. The chair of the board, if any, shall, when present, preside at all meetings of the board of directors and of the members. The chair shall have such other duties and powers as the board may specify.</li>
   <li><span class="nowrap">Vice-Chair</span> of the Board &#8211; The <span class="nowrap">vice-chair</span> of the board, if one is to be appointed, shall be a director. If the chair of the board is absent or is unable or refuses to act, the <span class="nowrap">vice-chair</span> of the board, if any, shall, when present, preside at all meetings of the board of directors and of the members. The <span class="nowrap">vice-chair</span> shall have such other duties and powers as the board may specify.</li>
   <li>President &#8211; If appointed, the president shall be the chief executive officer of the Corporation and shall be responsible for implementing the strategic plans and policies of the Corporation. The president shall, subject to the authority of the board, have general supervision of the affairs of the Corporation.</li>
   <li>Secretary &#8211; If appointed, the secretary shall attend and be the secretary of all meetings of the board, members and committees of the board. The secretary shall enter or cause to be entered in the Corporation's minute book, minutes of all proceedings at such meetings; the secretary shall give, or cause to be given, as and when instructed, notices to members, directors, the public accountant and members of committees; the secretary shall be the custodian of all books, papers, records, documents and other instruments belonging to the Corporation.</li>
   <li>Treasurer &#8211; If appointed, the treasurer shall have such powers and duties as the board may specify.</li>
</ol></li>



 <li><b>Officer Vacancies</b> <p>In the absence of a written agreement to the contrary, the board may remove, whether for cause or without cause, any officer of the Corporation. Unless so removed, an officer shall hold office until the earlier of:</p>

<ol type="a" class="lst-lwr-alph mrgn-lft-lg">
   <li>the officer's successor being appointed,</li>
   <li>the officer's resignation,</li>
   <li>such officer ceasing to be a director (if a necessary qualification of appointment) or</li>
   <li>such officer's death.</li>
</ol>

<p>If the office of any officer of the Corporation shall be or become vacant, the directors may, by resolution, appoint a person to fill such vacancy.</p>
</li>



 <li><b>Method of Giving Notice</b> <p>Any notice (which term includes any communication or document), other than notice of a meeting of members or a meeting of the board of directors, to be given (which term includes sent, delivered or served) pursuant to the Act, the articles, the <span class="nowrap">by-laws</span> or otherwise to a member, director, officer or member of a committee of the board or to the public accountant shall be sufficiently given:</p>

<ol type="a" class="lst-lwr-alph mrgn-lft-lg ">
   <li>if delivered personally to the person to whom it is to be given or if delivered to such person's address as shown in the records of the Corporation or in the case of notice to a director to the latest address as shown in the last notice that was sent by the Corporation in accordance with section&nbsp;128 (Notice of directors) or&nbsp;134 (Notice of change of directors);</li>
   <li>if mailed to such person at such person's recorded address by prepaid ordinary or air mail;</li>
   <li>if sent to such person by telephonic, electronic or other communication facility at such person's recorded address for that purpose; or</li>
   <li>if provided in the form of an electronic document in accordance with Part&nbsp;17 of the Act.</li>
</ol>

<p>A notice so delivered shall be deemed to have been given when it is delivered personally or to the recorded address as aforesaid; a notice so mailed shall be deemed to have been given when deposited in a post office or public letter box; and a notice so sent by any means of transmitted or recorded communication shall be deemed to have been given when dispatched or delivered to the appropriate communication company or agency or its representative for dispatch. The secretary may change or cause to be changed the recorded address of any member, director, officer, public accountant or member of a committee of the board in accordance with any information believed by the secretary to be reliable. The declaration by the secretary that notice has been given pursuant to this <span class="nowrap">by-law</span> shall be sufficient and conclusive evidence of the giving of such notice. The signature of any director or officer of the Corporation to any notice or other document to be given by the Corporation may be written, stamped, <span class="nowrap">type-written</span> or printed or partly written, stamped, <span class="nowrap">type-written</span> or printed.</p></li>



 <li><b>Invalidity of Provisions of this By-law</b> <p>The invalidity or unenforceability of any provision of these <span class="nowrap">By-laws</span> shall not affect the validity or enforceability of the remaining provisions.</p></li>



 <li><b>Omissions and Errors</b> <p>The accidental omission to give any notice to any member, director, officer, member of a committee of the board or public accountant, or the <span class="nowrap">non-receipt</span> of any notice by any such person where the Corporation has provided notice in accordance with the <span class="nowrap">by-laws</span> or any error in any notice not affecting its substance shall not invalidate any action taken at any meeting to which the notice pertained or otherwise founded on such notice.</p></li>



 <li><b>Mediation and Arbitration</b> <p>Disputes or controversies among members, directors, officers, committee members, or volunteers of the Corporation are as much as possible to be resolved in accordance with mediation and/or arbitration as provided in the section on dispute resolution mechanism of this <span class="nowrap">by-law</span>.</p></li>



 <li><b>Dispute Resolution Mechanism</b> <p>In the event that a dispute or controversy among members, directors, officers, committee members or volunteers of the Corporation arising out of or related to the articles or <span class="nowrap">by-laws,</span> or out of any aspect of the operations of the Corporation is not resolved in private meetings between the parties then without prejudice to or in any other way derogating from the rights of the members, directors, officers, committee members, employees or volunteers of the Corporation as set out in the articles, <span class="nowrap">by-laws</span> or the Act, and as an alternative to such person instituting a law suit or legal action, such dispute or controversy shall be settled by a process of dispute resolution as follows:</p>

<ul class="indent1 lst-spcd mrgn-lft-lg ">
   <li>The dispute or controversy shall first be submitted to a panel of mediators whereby the one party appoints one mediator, the other party (or if applicable the board of the Corporation) appoints one mediator, and the two mediators so appointed jointly appoint a third mediator. The three mediators will then meet with the parties in question in an attempt to mediate a resolution between the parties.</li>
   <li>The number of mediators may be reduced from three to one or two upon agreement of the parties.</li>
   <li>If the parties are not successful in resolving the dispute through mediation, then the parties agree that the dispute shall be settled by arbitration before a single arbitrator, who shall not be any one of the mediators referred to above, in accordance with the provincial or territorial legislation governing domestic arbitrations in force in the province or territory where the registered office of the Corporation is situated or as otherwise agreed upon by the parties to the dispute. The parties agree that all proceedings relating to arbitration shall be kept confidential and there shall be no disclosure of any kind. The decision of the arbitrator shall be final and binding and shall not be subject to appeal on a question of fact, law or mixed fact and law.</li>
</ul>

<p>All costs of the mediators appointed in accordance with this section shall be borne equally by the parties to the dispute or the controversy. All costs of the arbitrators appointed in accordance with this section shall be borne by such parties as may be determined by the arbitrators.</p></li>



 <li><b><span class="nowrap">By-laws</span> and Effective Date</b> <p>Subject to the articles, the board of directors may, by resolution, make, amend or repeal any <span class="nowrap">by-laws</span> that regulate the activities or affairs of the Corporation. Any such <span class="nowrap">by-law,</span> amendment or repeal shall be effective from the date of the resolution of directors until the next meeting of members where it may be confirmed, rejected or amended by the members by ordinary resolution. If the <span class="nowrap">by-law,</span> amendment or repeal is confirmed or confirmed as amended by the members it remains effective in the form in which it was confirmed. The <span class="nowrap">by-law,</span> amendment or repeal ceases to have effect if it is not submitted to the members at the next meeting of members or if it is rejected by the members at the meeting.</p>

<p>This section does not apply to a <span class="nowrap">by-law</span> that requires a special resolution of the members according to subsection&nbsp;197(1) (fundamental change) of the Act because such <span class="nowrap">by-law</span> amendments or repeals are only effective when confirmed by members.</p></li>

</ol>
</div>
</div>

        </div>
    )
}

export default Bylaws
